import React from 'react'

export default function UpvoteSection(props) {
    const upVote=()=>{
        fetch(`/api/articles/${props.articleName}/upvote`,{
            method: 'post'
        }).then(res=>res.json()).then(data=>props.setarticleInfo(data))
    }
    return (
        <div id="upvotes-section">
            <button onClick={()=>upVote()}>Add Upvote</button>
            <p>this post has been upvoted {props.upvotes}</p>
        </div>
    )
}
