import React, { Fragment } from 'react'

export default function CommentsList({comments}) {
    return (
        <Fragment>
            <h3>Comments</h3>
            {comments.map((comment,key)=>(
                <div className="comment" key={key}>
                    <h4>{comment.username}</h4>
                    <p>{comment.text}</p>
                </div>
            ))}
        </Fragment>       
    )
}
