import React,{useState,useEffect} from 'react'
import articleContent from './Article-Content'
import ArticleDetails from '../components/ArticleDetails'
import NotFoundPage from './NotFoundPage'
import CommentsList from '../components/CommentsList'
import UpvoteSection from '../components/UpvoteSection'
import CommentsSection from '../components/CommentsSection'

export default function ArticlePage({match}) {
    const name=match.params.name
    const article = articleContent.find(article=> article.name===name)
    const otherArticles=articleContent.filter(article=> article.name!==name)

    const [articleInfo, setarticleInfo] = useState({upvotes: 0, comments: []})

    useEffect(() => {
        fetch(`/api/articles/${name}`)
        .then(response=> response.json())
        .then(data=>setarticleInfo(data))
    }, [name])
    
    if(!article) return <NotFoundPage/>

    return (
        <div>
            <h1>{article.title}</h1>
            <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setarticleInfo={setarticleInfo} />
            {article.content.map((para,key)=>(<p key={key}>{para}</p>))}
            <CommentsList comments={articleInfo.comments}/>
            <CommentsSection articleName={name} setarticleInfo={setarticleInfo}/>
            <h3>Other Articles</h3>
            <ArticleDetails articles={otherArticles}/>
        </div>
    )
}
